@font-face {
  font-family: 'openSans';
  src: url('./Open_Sans/OpenSans-VariableFont_wdth,wght.ttf');
}

* {
  font-family: 'openSans';
  box-sizing: border-box;
}

.submit{
  display:flex;
  justify-content: center;
  background-color: black;
  border: 1px white solid;
  width:210px;
  font-size:22px;
  align-items: center;
  color:white;
  border-radius: 8px;
}
.signIn{
  display:flex;
  font-size:22px;
  margin-left:16px;
  justify-content: center;
  border:none;
  width:120px;
  height:45px;
  align-items: center;
  background-color: #4C5FD5;
  color:white;
  border-radius: 8px;
}
.header{
  display:flex;
  margin:0;
  left:0;
  top:0;
  background-color: black;
  color:white;
  font-size:22px;
  height:88px;
  padding-left:30px;
  padding-right:30px;
}

.header__controls{
  width:70%;
  display:flex;
  justify-content: flex-end;
}

.searchSection__content__input{
  display:flex;
  border-radius: 8px;
  margin-left:auto;
  margin-right:auto;
  max-width:720px;
  height:66px;
  background-color: white;
  border:2px solid grey;
}
.searchSection__content{
  width:90%;
  margin:auto;
}
.searchSection{
  display: flex;
  width:100%;
  padding-top:80px;
  padding-bottom:115px;
  background-color: #dadbf1;
}

h4{
  margin: 3rem 0 1rem;
}
.item-block__content{
  font-size:24px;
}
.item-block{
  position:relative;
  padding: 0 1.5rem 0 0;
  width:100%;
}
img{
  max-height:120px; 
  margin-right:30px; 
  max-width:100%;
}
.item-icon{
  display:flex;
  flex-direction: column;
  margin-top:40px;
}
.item{
  display:flex;
  min-height:115px;
  width:49%;
  margin-bottom:3rem;
  padding-right:15px;
}

a{
  color:white;
  text-decoration: none;
  display:block;
}
.footer-title{
  margin: 3rem 0 1rem;
  font-size:24px;
  line-height: 28px;
}
.footer-column{
  display:inline-block;
  width:20%;
  vertical-align: top;
}
.footer__content{
  margin: 0 auto;
  width:90%;
  max-width:1900px;
  position:relative;
  padding-bottom:100px;
}
.footer{
  padding-bottom:60px;
  padding-left:30px;
  padding-right:30px;
  background-color:black;
  color:white;
}

.burgeri{
  border-radius: 5px;
  width: 25px;
  height: 3px;
  background-color: white;
  margin: 7px;
}

.dropdown{
  position:absolute;
  display:inline-block;
  top:0;
  height:40px;
  width:40px;
}
.dropdown__sisältö{
  display:none;
  position:absolute;
  background-color: rgb(255, 255, 255);
  min-width:90vw;
  min-height:100px;
  top:129px;

}
button{
  padding:0;
  width:40px;
  height:40px;
  border:0;
  margin:0;
  background-color:black;
}

button:hover{
  background-color: rgb(200, 200, 200);
}

.dropdown:hover .dropdown__sisältö{
  display:block;
}